import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "primereact/button";
import { Column } from "primereact/column";
import { DataTable, DataTableSelection } from "primereact/datatable";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { Toast } from "primereact/toast";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { manufacturerActions } from "../../store/manufacturer/manufacturerSlice";
import { IAppState } from "../../models/appState";
import { IManufacturer } from "../../models/manufacturer";

const ManufacturerList: React.FC = () => {
  const defaultItem: IManufacturer = { id: "" };
  const [selectedItem, setSelectedItem] = useState<
    DataTableSelection<IManufacturer[]>
  >([defaultItem]);
  const [item, setItem] = useState(defaultItem);
  const [globalFilter, setGlobalFilter] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const navigate = useNavigate();
  const dt = useRef(null);
  const toast = useRef(null);
  const manufacturers: IManufacturer[] = useAppSelector(
    (state: IAppState) => state.manufacturers.manufacturers,
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(manufacturerActions.getManufacturers());
  }, [dispatch]);

  const onAddClick = () => {
    navigate("/manufacturer/add");
  };

  const onExportClick = () => {
    (dt.current as any).exportCSV();
  };

  const editItem = (rowData: any) => {
    navigate(`/manufacturer/edit/${rowData.id}`);
  };

  const confirmDeleteItem = (rowData: IManufacturer) => {
    setItem(rowData);
    setDeleteDialog(true);
  };

  const hideDeleteDialog = () => {
    setDeleteDialog(false);
  };

  const onSearchInput = (e: any) => {
    (dt.current as any).reset();
    setGlobalFilter(e.target.value);
  };

  const deleteItem = async () => {
    // TODO: Add check if manufacturer is in use
    dispatch(manufacturerActions.deleteManufacturer(item.id));
    hideDeleteDialog();
    setItem(defaultItem);
    (toast.current as any).show({
      severity: "success",
      summary: "Successful",
      detail: "Item has been deleted",
      life: 3000,
    });
    setTimeout(() => {
      dispatch(manufacturerActions.getManufacturers());
    }, 300);
  };

  const header = (
    <div className="table-header">
      <span className="p-input-icon-left">
        <i className="pi pi-search" />
        <InputText
          type="search"
          onInput={onSearchInput}
          placeholder="Search..."
        />
      </span>
    </div>
  );

  const actionBodyTemplate = (rowData: any) => {
    return (
      <>
        <Button
          icon="pi pi-pencil"
          className="p-button-rounded p-button-outlined mr-2"
          tooltip="Edit"
          onClick={() => editItem(rowData)}
        />
        <Button
          icon="pi pi-trash"
          className="p-button-rounded p-button-outlined"
          tooltip="Delete"
          onClick={() => confirmDeleteItem(rowData)}
        />
      </>
    );
  };

  const deleteDialogFooter = (
    <>
      <Button
        label="No"
        icon="pi pi-times"
        className="p-button-text"
        onClick={hideDeleteDialog}
      />
      <Button
        label="Yes"
        icon="pi pi-check"
        className="p-button-text"
        onClick={deleteItem}
      />
    </>
  );

  return (
    <>
      <Toast ref={toast} />
      <div className="grid pt-2">
        <div className="col text-left">
          <h3 className="caption">Manufacturers</h3>
        </div>
        <div className="col text-right">
          <Button
            icon="pi pi-plus"
            className="p-button-rounded p-button-outlined"
            tooltip="Add Manufacturer"
            onClick={onAddClick}
          />
          &nbsp;
          <Button
            icon="pi pi-external-link"
            className="p-button-rounded p-button-outlined"
            tooltip="Export Manufacturers"
            onClick={onExportClick}
          />
        </div>
      </div>
      <DataTable
        ref={dt}
        dataKey="id"
        value={manufacturers}
        paginator
        rows={10}
        rowsPerPageOptions={[10, 25, 50, 100]}
        selectionMode="single"
        selection={selectedItem}
        onSelectionChange={(e) => setSelectedItem(e.value)}
        resizableColumns
        globalFilter={globalFilter}
        header={header}
        showGridlines
      >
        <Column field="name" header="Manufacturer Name" sortable></Column>
        <Column field="sortOrder" header="Sort Order" sortable></Column>
        <Column
          body={actionBodyTemplate}
          exportable={false}
          style={{ minWidth: "8rem", width: "30%" }}
        ></Column>
      </DataTable>
      <p>* Table columns are sortable and resizable.</p>
      <Dialog
        visible={deleteDialog}
        style={{ width: "450px" }}
        header="Confirm"
        modal
        footer={deleteDialogFooter}
        onHide={hideDeleteDialog}
      >
        <div className="confirmation-content">
          <i
            className="pi pi-exclamation-triangle mr-3"
            style={{ fontSize: "2rem" }}
          />
          <strong>Are you sure you want to delete this item?</strong>
        </div>
      </Dialog>
    </>
  );
};

export default ManufacturerList;
